import * as React from "react";
import Layout from "../components/layout";

const ContactPage = () => {
  return (
    <Layout pageTitle="Contact">
      <div className="row">
        <div className="col-4">
          <p className="fs-2 px-4">
            We are constantly on the hunt for clients who are serious about
            growing their business.
          </p>
          <p className="fs-2 px-4">Do you have a challenge for us ?</p>
          <p className="fs-2 px-4">hello@lane7.co</p>
        </div>
        <div className="col-8">
          <form className="row g-3">
            <div className="col-md-6 col-sm-12">
              <label for="naam" className="form-label">
                Name
              </label>
              <input
                type="text"
                className="form-control border-0 border-bottom"
                id="naam"
              />
            </div>
            <div className="col-md-6 col-sm-12">
              <label for="email" className="form-label">
                Email
              </label>
              <input
                type="email"
                className="form-control border-0 border-bottom"
                id="email"
              />
            </div>
            <div className="col-md-6 col-sm-12">
              <label for="company" className="form-label">
                Company
              </label>
              <input
                type="text"
                className="form-control border-0 border-bottom"
                id="company"
              />
            </div>
            <div className="col-md-6 col-sm-12">
              <label for="mobile" className="form-label">
                Mobile
              </label>
              <input
                type="number"
                className="form-control border-0 border-bottom"
                id="mobile"
              />
            </div>
            <div className="col-12">
              <label for="inputAddress" className="form-label">
                Tell us a bit about your company and project
              </label>
              <textarea
                className="form-control border-0 border-bottom"
                id="describe"
                placeholder=""
              />
            </div>

            <div className="col-12">
              <label for="services" className="form-label">
                What services are you looking for ?
              </label>
              <div className="form-check">
                <input
                  className="form-check-input"
                  type="checkbox"
                  id="gridCheck"
                />
                <label className="form-check-label" for="gridCheck">
                  Service
                </label>
              </div>
            </div>
            <div className="col-12">
              <button
                type="button"
                className="btn btn-warning btn-lg mb-4 fw-bold rounded-pill"
              >
                Submit
              </button>
            </div>
          </form>
        </div>
      </div>
    </Layout>
  );
};

export default ContactPage;

export const Head = () => <title>Contact</title>;
